export default {
    created() {
        this.getData()
    },
    data() {
        return {
            taskList: [],
            URL:getApp().globalData.URL,
        };
    },
    methods: {
        async getData() {
            await this.$u.get('/my_push_dy_task').then(res => {
                this.taskList = res.data
            })
        },
        del(dy_task_id){
            uni.showModal({
                title:"提示",
                content:"确认下架？",
                cancelText:"继续展示",
                confirmText:"下架",
                success:(res)=>{
                    if(res.confirm){
                        this.$u.post('/delete_dy_task',{dy_task_id}).then(res=>{
                            if(res.errorCode===0){
                                uni.showToast({
                                    title:"下架成功"
                                })
                            }
                            this.getData()
                        })
                    }
                    
                }
            })

        },
        open(dy_task_id){
           this.$u.route('pages/myPushDyTaskOrder/myPushDyTaskOrder', {
           	dy_task_id
           });
        }
    }
}
